* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  text-align: center;
  font-family: 'Open Sans', Arial, sans-serif;
  // height: 150vh;
}

h1 {
  font-size: 21px;
  font-weight: bold;
  padding: 8px;
  margin-bottom: 0;
}

h2 {
  font-size: 18px;
  font-weight: bold;
  padding: 3px;
}

p {
  text-align: center;
  padding: 5px 3px;
}

.carousel {
  // width: 100%;
  // height: 212px;
  // border: 3px solid black;

  img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    // margin-bottom: 20px;
  }
}

.contact {
  padding: 25px 0;
  background-color: rgba(12, 89, 119, 0.9);
  box-shadow: inset 11px 18px 115px 25px rgba(0, 0, 0, 0.65);
  color: #fff;
  border-top: 3px solid #000;

  h2 {
    font-size: 18px;
    font-weight: bold;
  }

  p {
    margin-bottom: 0;
  }
}